import React from "react";
import Cover from "../img/cover-home.jpeg";
import "./home.css";
class Home extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-elements animated lightSpeedIn delay-0.5s">
        <button onClick={this.eventOfClick} className="show-nav-home">
          <i className="far fa-bars"> </i>{" "}
        </button>{" "}
        <div className="cover">
          <img src={Cover} alt="cover" />
          <div className="description">
            <h2> Hi, my name is Antonio </h2>{" "}
            <p> I 'm a Front-end web Developer</p>{" "}
            <p> I love coding and create beautiful website </p>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}
export default Home;
