import React from "react";
import "./work.css";
import Weather from "../img/weather-app.png";
import Tip from "../img/tip-calculator.png";
import Count from "../img/count-down.png";
import Exact from "../img/exact-time.png";
import Audi from "../img/audi.png";
import Todo from "../img/to-do-list.png";

class Work extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-work animated zoomInUp delay-0.5s">
        <button onClick={this.eventOfClick} className="show-nav-work">
          <i className="far fa-bars"> </i>{" "}
        </button>{" "}
        <div className="title-work animated rotateIn delay-1s">
          <h4> My works </h4>{" "}
        </div>{" "}
        <div className="container-image">
          <div className="weather animated rotateIn delay-1s">
            <a
              href="https://asusoftware.github.io/Weather_App/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Weather} alt="weather" />
            </a>{" "}
          </div>{" "}
          <div className="tip-calculator animated rotateIn delay-2s">
            <a
              href="https://asusoftware.github.io/Tip-Calculator/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Tip} alt="tip" />
            </a>{" "}
          </div>{" "}
          <div className="count-down animated rotateIn delay-3s">
            <a
              href="https://asusoftware.github.io/Count-Down/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Count} alt="count" />
            </a>{" "}
          </div>{" "}
        </div>{" "}
        <div className="container-image-down">
          <div className="exact-time animated rotateIn delay-4s">
            <a
              href="https://asusoftware.github.io/Ora_Exacta/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Exact} alt="exact" />
            </a>{" "}
          </div>{" "}
          <div className="audi animated rotateIn delay-5s">
            <a
              href="https://asusoftware.github.io/Audi-Performance/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Audi} alt="audi" />
            </a>{" "}
          </div>{" "}
          <div className="to-do animated rotateIn delay-5s">
            <a
              href="https://asusoftware.github.io/ToDoAppModificat/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img src={Todo} alt="todo" />
            </a>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}

export default Work;
