import React from "react";
import "./about.css";
import CountUp from "react-countup";

class About extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      first: <CountUp delay={1} end={1345} />,
      second: <CountUp delay={1} end={600} />,
      third: <CountUp delay={1} end={12} />
    };
  }

  eventOfClick() {
    const nav = document.getElementsByClassName(
      "nav animated fadeInLeft delay-0.5s"
    )[0];
    nav.style.display = "flex";

    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-about">
        <button onClick={this.eventOfClick} className="show-nav-about">
          <i className="far fa-bars"></i>
        </button>
        <div className="title">
          <h6>About Us</h6>
          <h4>Who I am?</h4>
        </div>
        <div className="description-life">
          <p>
            Hi, I'm Antonio Lucian, I'm a Frond-end web developer. I am 21 years
            old and live near Rome. As a child I enjoyed studying computer
            science and creating interactive websites. I finished the
            electronics and electrical engineering high school where I studied
            the components, circuits, and finally programmed them to do what I
            wanted them to do. I started with Arduino, which in practice for
            those who do not know it and a programmable circuit in which we can
            give it inputs and expect the outputs. With him I have carried out
            many projects including a robot spider that through a joystick did
            what I wanted, that is, to make him walk slowly / quickly and make
            him dance. After I finished high school I settled in to study
            Front-end web development in which I carried out many personal
            projects, including a weather app that giving input the name of the
            user and his position and able to memorize everything and every time
            that you enter the website keep in mind your name and output
            personalized messages and all the weather information for that
            location. I also did courses on Freecodecamp where I did 300 hours
            of web designer and 300 hours of Javascript. My passion is to create
            interactive websites and also work with servers.
          </p>
        </div>
        <div className="middle">
          <div className="counting-sec">
            <div className="inner-width">
              <div className="col">
                <i className="far fa-code"></i>
                <div className="num">{this.state.first}</div>
                Lines of code
              </div>

              <div className="col">
                <i className="fab fa-free-code-camp"></i>
                <div className="num">{this.state.second}</div>
                Freecodecamp hours
              </div>

              <div className="col">
                <i className="fas fa-folder"></i>
                <div className="num">{this.state.third}</div>
                Projects
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default About;
