import React from "react";
import "./education.css";
import Certificat1 from "../img/certificat1.png";

class Education extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-education">
        <button onClick={this.eventOfClick} className="show-nav-education">
          <i className="far fa-bars"> </i>{" "}
        </button>{" "}
        <div className="title-education">
          <h4 className="animated zoomInDown delay-0.5s"> Education </h4>{" "}
        </div>{" "}
        <div className="school">
          <h3 className="electro">
            DIPLOMA IN ELECTRONICS AND ELECTRICAL ENGINEERING{" "}
          </h3>{" "}
          <h3 className="free"> FREECODECAMP CERTIFICATIONS </h3>{" "}
          <h3 className="hight"> HIGHT SCHOOL SECONDARY EDUCATION </h3>{" "}
        </div>{" "}
        <div className="container-certifications">
          <div className="first-certificate">
            <a
              href="https://www.freecodecamp.org/certification/asu_software/responsive-web-design"
              rel="noopener noreferrer"
              target="_blank"
            >
              <img className="certificat1" src={Certificat1} alt="certificat" />
            </a>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}

export default Education;
