import React from "react";
import "./skills.css";

class Skills extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-skill animated zoomInDown delay-0.5s">
        <button onClick={this.eventOfClick} className="show-nav-skills">
          <i className="far fa-bars"></i>{" "}
        </button>{" "}
        <div className="title-skills animated rollIn delay-0.5s"> Skills </div>{" "}
        <div className="container-progress animated jackInTheBox delay-1s">
          <div className="detail">
            <p className="first"> HTML5 </p> <p> 85 % </p>{" "}
          </div>
          <div className="container-bar">
            <div className="progress-bar-html"> </div>{" "}
          </div>
          <div className="detail">
            <p className="first"> CSS3 </p> <p> 70 % </p>{" "}
          </div>{" "}
          <div className="container-bar">
            <div className="progress-bar-css"> </div>{" "}
          </div>
          <div className="detail">
            <p className="first"> JAVASCRIPT </p> <p> 65 % </p>{" "}
          </div>{" "}
          <div className="container-bar">
            <div className="progress-bar-js"> </div>{" "}
          </div>
          <div className="detail">
            <p className="first"> REACTJS </p> <p> 50 % </p>{" "}
          </div>{" "}
          <div className="container-bar">
            <div className="progress-bar-react"> </div>{" "}
          </div>
          <div className="detail">
            <p className="first"> PHOTOSHOP </p> <p> 75 % </p>{" "}
          </div>{" "}
          <div className="container-bar">
            <div className="progress-bar-photoshop"> </div>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}

export default Skills;
