import React from "react";
import "./contact.css";

class Contact extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-contact animated fadeIn delay-0.5s">
        <button onClick={this.eventOfClick} className="show-nav-contact">
          <i className="far fa-bars"> </i>{" "}
        </button>{" "}
        <div className="title-contact animated lightSpeedIn delay-1s">
          <h4> Contact Me for more information </h4>{" "}
        </div>{" "}
        <div className="container-information">
          <div className="contact-method animated fadeInDown delay-1s">
            <i className="fas fa-envelope"> </i>{" "}
            <span> web.antonio.popa @gmail.com </span>{" "}
          </div>
          <div className="contact-method animated fadeInDown delay-2s">
            <i className="far fa-mobile"> </i> <span> +39 3500207845 </span>{" "}
          </div>
          <div className="contact-method animated fadeInDown delay-3s">
            <i className="fas fa-map-marker-alt"> </i>{" "}
            <span> Olevano Romano, Rome </span>{" "}
          </div>{" "}
        </div>
        <div className="social animated fadeIn delay-1s">
          <div className="container-social">
            <a
              href="https://github.com/AsuSoftware"
              rel="noopener noreferrer"
              target="_blank"
            >
              <i className="fab fa-github"> </i>{" "}
            </a>{" "}
          </div>
          <div className="container-social">
            <a
              href="https://www.facebook.com/AntonioLucian24"
              rel="noopener noreferrer"
              target="_blank"
            >
              <i className="fab fa-facebook-square"> </i>{" "}
            </a>{" "}
          </div>
          <div className="container-social">
            <a
              href="https://www.instagram.com/antonio_lucian13/"
              rel="noopener noreferrer"
              target="_blank"
            >
              <i className="fab fa-instagram"> </i>{" "}
            </a>{" "}
          </div>{" "}
        </div>{" "}
      </div>
    );
  }
}

export default Contact;
