import React from "react";
import "./services.css";

class Services extends React.Component {
  eventOfClick() {
    const nav = document.getElementsByClassName("nav")[0];
    nav.style.display = "flex";
    window.addEventListener("click", function(event) {
      if (event.target.tagName.toLowerCase() === "a") {
        nav.style.display = "none";
      } else if (
        event.target.tagName.toLowerCase() === "img" ||
        event.target.tagName.toLowerCase() === "div" ||
        event.target.tagName.toLowerCase() === "p" ||
        event.target.tagName.toLowerCase() === "h6" ||
        event.target.tagName.toLowerCase() === "h4"
      ) {
        nav.style.display = "none";
      }
    });
  }

  render() {
    return (
      <div className="container-services">
        <button onClick={this.eventOfClick} className="show-nav-service">
          <i className="far fa-bars"></i>
        </button>
        <div className="title-services">
          <h4>Here are some of my Services</h4>
        </div>
        <div className="container-box">
          <div className="box1">
            <div className="logo1">
              <i className="fas fa-lightbulb-on"></i>
            </div>
            <h3>INNOVATIVE IDEAS</h3>
            <p>I always try to find new ideas and implement on the website</p>
          </div>

          <div className="box2">
            <div className="logo2">
              <i className="fas fa-credit-card-front"></i>
            </div>
            <h3>Front End</h3>
            <p>
              I carry out Front-end activities, everything that holds the vizual
              part of the website, and I make them fluid and very friend-user
            </p>
          </div>

          <div className="box3">
            <div className="logo3">
              <i className="far fa-mobile"></i>
            </div>
            <h3>Mobile Responsive</h3>
            <p>
              I also offer responsive mobile services, in which I make websites
              also supported for mobile versions
            </p>
          </div>
        </div>

        <div className="container-animation">
          <div className="background">
            <div className="icon-center">
              <i className="far fa-code"></i>
            </div>
            <div className="container-circle2">
              <div className="container-circle">
                <div className="javascript-icon">
                  <i className="fab fa-js"></i>
                </div>

                <div className="react-icon">
                  <i className="fab fa-react"></i>
                </div>

                <div className="css-icon">
                  <i className="fab fa-css3"></i>
                </div>
              </div>

              <div className="html-icon">
                <i className="fab fa-html5"></i>
              </div>

              <div className="npm-icon">
                <i className="fab fa-npm"></i>
              </div>

              <div className="git-icon">
                <i className="fab fa-github"></i>
              </div>

              <div className="adobe-icon">
                <i className="fab fa-adobe"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Services;
